async function populateReferrerLinkinHTML(userAddress) {
    let referrerValue = userAddress; //which will be 'logged out' if user is logged out
    // if user is logged-in we change the value of refferValue
    if (isUserConnected()) referrerValue = await getReferrer(userAddress);
    const referrerFields = document.querySelectorAll('.refer-referrer');
    referrerFields.forEach(el => {
        // in this example - the userAddress = logged out
        el.innerHTML = referrerValue;
    });

    // uncomment the below line - to check if the addReferrerBtn will show when the reffere is 0x00...00 or not
    // referrerValue = '0x0000000000000000000000000000000000000000'
    /**
     * if referrer is 0x0000000000000000000000000000000000000000
     * then show the ability to change / setReferrer
     * else keep it hidden
     */
    const defaultReferrer = '0x0000000000000000000000000000000000000000';
    const setRefferrerBtn = document.querySelector('#add-referrer');
    if (referrerValue === defaultReferrer) setRefferrerBtn.classList.remove('hidden')
    else setRefferrerBtn.classList.add('hidden')
}


async function getReferrer(userAddress) {
    const ABI_getReferrer = [{ "inputs": [{ "internalType": "address", "name": "_user", "type": "address" }], "name": "getReferrer", "outputs": [{ "internalType": "address", "name": "", "type": "address" }], "stateMutability": "view", "type": "function" }];

    let options = {
        contractAddress: REFER_CONTRACT_ADDRESS_TESTNET,
        functionName: 'getReferrer',
        abi: ABI_getReferrer,
        params: {
            _user: userAddress,
        }
    }

    return await Moralis.executeFunction(options);
}

