async function addReferrer() {
    const referrerAddress = document.querySelector('#add-referrer-address');
    const errContainer = document.querySelector('#add-referrer-err');
    const errMsgContainer = document.querySelector('#add-referrer-msg');

    if (!referrerAddress.value) {
        showErrMsg(errContainer, errMsgContainer, 'Empty Field!');
        return;
    } else if (referrerAddress.value.length <= 6) {
        showErrMsg(errContainer, errMsgContainer, 'Invalid Address!');
        return;
    }
    // at this point the refer address isn't empty and length is > 6 characters

    try {
        let result = await setReferAddress(referrerAddress.value);
        populateReferrerLinkinHTML(getUserWalletAddress());
        console.log(result);

    } catch (error) {
        console.error(error);
        showErrMsg(errContainer, errMsgContainer, 'Invalid Address!!!');
    }
}
async function setReferAddress(referrerAddress) {

    const ABI_totalReferralCommissions = [{ "inputs": [{ "internalType": "address", "name": "referAddress", "type": "address" }], "name": "addReferAddress", "outputs": [], "stateMutability": "nonpayable", "type": "function" }];
    console.log(`setReferAddress to - ${referrerAddress}`);
    let options = {
        contractAddress: REFER_CONTRACT_ADDRESS_TESTNET,
        functionName: 'addReferAddress',
        abi: ABI_totalReferralCommissions,
        params: {
            referAddress: referrerAddress,
        }
    }

    let transaction;
    try {
        // await Moralis.enableWeb3();
        transaction = await Moralis.executeFunction(options);
    } catch (error) {
        console.error(error);
    }

    return await transaction.wait();
}

